
body {
  margin: 0;
}
#resume {
  padding: 60px 0;
  background-color: #eee;
  color: #4e959f;
  font-family: "Microsoft JhengHei", Helvetica, Arial, sans-serif;
}
.fullpage {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #c1e4e9;
  text-align: center;
}
.page {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  -webkit-transition: background .3s ease-in, -webkit-transform .5s ease-out;
  transition: background .3s ease-in, -webkit-transform .5s ease-out;
  transition: transform .5s ease-out, background .3s ease-in;
  transition: transform .5s ease-out, background .3s ease-in, -webkit-transform .5s ease-out;
}
.current, .pre, .next {
  opacity: 1;
}
.current {
  -webkit-transform: translate3d(0,0,0);
          transform: translate3d(0,0,0);
}
.pre {
  -webkit-transform: translate3d(0, -100%, 0);
          transform: translate3d(0, -100%, 0);
  background: rgba(0,0,0,.3);
}
.next {
  -webkit-transform: translate3d(0,100%,0);
          transform: translate3d(0,100%,0);
  background: rgba(0,0,0,.3);
}
.title {
  font-size: 3em;
  color: #fff;
  text-shadow: 1px 2px #38a1db;
}
.content {
  padding: 20px;
}
a {
  text-decoration: none;
  color: #4e959f;
}



